import React, { useState } from 'react';
import '../styles/Navbar.scss';
import { useQuery } from 'react-query';
import { Link } from 'react-router-dom';
import Logo from '../assets/images/logo.png';
import { useTranslation } from 'react-i18next';
import { getName } from '../languages/language';
import { fetchCategoriesData, fetchProductsData } from '../api/Api';
import { Close, FilterList, MenuRounded, Search } from '@mui/icons-material';

const Navbar = ({ changeScrollFaq, changeScrollAbout, changeScrollProducts, changeScrollAdvantages }) => {

    // languages

    const { i18n, t } = useTranslation();
    const [language, setLanguage] = useState(false);
    const [langTitle, setLangTitle] = useState(localStorage.getItem('i18nextLng').toUpperCase())

    function changeLangValue(item) {
        setLanguage(false);
        i18n.changeLanguage(item);
        setLangTitle(localStorage.getItem('i18nextLng').toUpperCase())
    };

    // navbar menu or navbar-nav

    const [showMenu, setShowMenu] = useState(false);
    const [showCatalog, setShowCatalog] = useState(false);

    // data of Products

    const dataProducts = useQuery('products', fetchProductsData);

    // data of Products

    const dataCategories = useQuery('categories', fetchCategoriesData);

    // search filter

    const [inputValue, setInputValue] = useState("");
    const [showSearchBar, setShowSearchBar] = useState(false);

    function filterData(e) {
        setInputValue(e.target.value)
    };

    const dataProds = dataProducts?.data?.data?.filter(c => getName(c).toLowerCase().includes(inputValue.toLowerCase()));
    const dataCategs = dataCategories?.data?.data?.filter(c => getName(c).toLowerCase().includes(inputValue.toLowerCase()));

    function getSearchBar() {
        setInputValue("");
        setShowSearchBar(false);
    };

    return (
        <div className='Navbar'>
            <div className="top-navbar cont">
                <div className='left'>
                    <Link to="/" onClick={() => window.scrollTo(0, 0)} className="logo">
                        <img src={Logo} alt="logo" className="img" />
                    </Link>
                    <div className={`links ${showMenu && "show-navbar"}`}>
                        <Link to="/" className='link text' onClick={changeScrollAbout}>{t("b1")}</Link>
                        <Link to="/categories/1" className='link text'>{t("b2")}</Link>
                        <Link to="/" className='link text' onClick={changeScrollProducts}>{t("b3")}</Link>
                        <Link to="/" className='link text' onClick={changeScrollAdvantages}>{t("b4")}</Link>
                        <Link to="/" className='link text' onClick={changeScrollFaq}>{t("b5")}</Link>
                        <a href='#contacts' className='link text'>{t("b6")}</a>
                        <div className="language responsive">
                            <div className="lang-title text" onClick={() => setLanguage(!language)}>{langTitle}</div>
                            {language &&
                                <div className="lang-menu">
                                    <div className="lang-items text" onClick={() => changeLangValue("uz")}>UZ</div>
                                    <div className="lang-items text" onClick={() => changeLangValue("ru")}>RU</div>
                                    <div className="lang-items text" onClick={() => changeLangValue("en")}>EN</div>
                                </div>
                            }
                            {language &&
                                <div className="contrast-0" onClick={() => setLanguage(!language)}></div>
                            }
                        </div>
                        <a href={`tel:${t("tel_num")}`} className="tel-btn text responsive">{t("tel_num")}</a>
                        <Close className="close-icon" onClick={() => setShowMenu(false)} />
                    </div>
                    <MenuRounded className="menu-icon" onClick={() => setShowMenu(true)} />
                </div>
                <div className="right">
                    <div className="language">
                        <div className="lang-title text" onClick={() => setLanguage(!language)}>{langTitle}</div>
                        {language &&
                            <div className="lang-menu">
                                <div className="lang-items text" onClick={() => changeLangValue("uz")}>UZ</div>
                                <div className="lang-items text" onClick={() => changeLangValue("ru")}>RU</div>
                                <div className="lang-items text" onClick={() => changeLangValue("en")}>EN</div>
                            </div>
                        }
                        {language &&
                            <div className="contrast-0" onClick={() => setLanguage(!language)}></div>
                        }
                    </div>
                    <a href={`tel:${t("tel_num")}`} className="tel-btn text">{t("tel_num")}</a>
                </div>
            </div>
            <div className="middle-navbar cont">
                <div className="catalog">
                    <button className='category-btn name' onClick={() => setShowCatalog(!showCatalog)}><FilterList className='icon' /> {t("katalog")}</button>
                    {showCatalog &&
                        <div className="catalog-bar">
                            {dataCategories?.data?.data?.map((item) => (
                                <Link key={item.id} to={`/categories/${item.id}`} className='text catalog-link' onClick={() => setShowCatalog(false)}>{getName(item)}</Link>
                            ))}
                        </div>
                    }
                    {showCatalog &&
                        <div className="contrast-0" onClick={() => setShowCatalog(false)}></div>
                    }
                </div>
                <div className="searching">
                    <label htmlFor="search" onClick={() => setShowSearchBar(true)}><Search className='icon' /></label>
                    <input type="text" id='search' className='name' value={inputValue} onChange={(e) => filterData(e)} onClick={() => setShowSearchBar(true)} placeholder={`${t("qidiruv")}`} />
                    {showSearchBar &&
                        <div className="search-bar">
                            {dataProds.map((item) => (
                                <Link key={item.id} to={`/products/${item.id}`} className='text search-link' onClick={getSearchBar}>{getName(item)}</Link>
                            ))}
                            {dataCategs.map((item) => (
                                <Link key={item.id} to={`/categories/${item.id}`} className='text search-link' onClick={getSearchBar}>{getName(item)}</Link>
                            ))}
                        </div>
                    }
                    {showSearchBar &&
                        <div className="contrast-0" onClick={getSearchBar}></div>
                    }
                </div>
            </div>
            {/* <div className="bottom-navbar cont">
                <div className="line"></div>
                <Link to="/" className='link text'></Link>
                <Link to="/" className='link text'></Link>
                <Link to="/" className='link text'> </Link>
                <Link to="/" className='link text'> </Link>
                <Link to="/" className='link text'> </Link>
                <Link to="/" className='link text'> </Link>
                <div className="line"></div>
            </div> */}
        </div>
    );
};

export default Navbar;