import React from 'react';
import '../styles/Categories.scss';
import { useQuery } from 'react-query';
import { Skeleton } from '@mui/material';
import { useTranslation } from 'react-i18next';
import { getName } from '../languages/language';
import { Link, useParams } from 'react-router-dom';
import { fetchCategoriesData, fetchProductsData } from '../api/Api';

const Categories = () => {

    const { id } = useParams();

    // i18next

    const { t } = useTranslation();

    // data of Products

    const dataProducts = useQuery('products', fetchProductsData);

    // data of categories

    const { isLoading, data } = useQuery('categories', fetchCategoriesData);

    if (isLoading) {
        <div className="Categories parent">
            <div className="wrapper">
                <div className="categs">
                    {[1, 2, 3, 4, 5, 6, 7, 8].map((item, index) => (
                        <div key={index} className={`category name`}>
                            <Skeleton variant='text' sx={{ bgcolor: 'grey.800' }} width="100%" height="2vw" className={`category name`} />
                        </div>
                    ))}
                </div>
                <div className="products">
                    {[1, 2, 3, 4, 5, 6, 7, 8].map((item, index) => (
                        <div key={index} className="product">
                            <Skeleton variant='rounded' sx={{ bgcolor: 'grey.800' }} width="100%" className="img" />
                            <p className="text">
                                <Skeleton variant='text' sx={{ bgcolor: 'grey.800' }} width="100%" />
                            </p>
                            <Skeleton variant='rounded' sx={{ bgcolor: 'grey.800' }} width="100%" height="3vw" className={`explore text`} />
                        </div>
                    ))}
                </div>
            </div>
        </div>
    }

    return (
        <div className='Categories parent'>
            <div className="wrapper">
                <div className="categs">
                    {data?.data.map((item) => (
                        <Link key={item.id} to={`/categories/${item.id}`} className={`category name ${item.id == id && "active-category"}`}>{getName(item)}</Link>
                    ))}
                </div>
                <div className="products">
                    {dataProducts?.data?.data?.filter((c) => c.category_id == id).map((item) => (
                        <Link to={`/products/${item.id}`} key={item.id} className="product">
                            <img src={item.image} alt="img" className="img" />
                            <p className="text">{getName(item)}</p>
                            <button className='explore text'>{t("batafsil")}</button>
                        </Link>
                    ))}
                </div>
            </div>
        </div>
    );
};

export default Categories;