import React from 'react';
import '../styles/Header.scss';
import Slider from 'react-slick';
import { useQuery } from 'react-query';
import { Skeleton } from '@mui/material';
import { useTranslation } from 'react-i18next';
import { getName } from '../languages/language';
import { fetchHeaderSliderData } from '../api/Api';

const Header = () => {

    const settings = {
        speed: 2000,
        dots: false,
        autoplay: true,
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        cssEase: "linear",
        autoplaySpeed: 6000,
        pauseOnHover: false,
    };

    // i18next

    const { t } = useTranslation();

    // data of categories

    const { isLoading, data } = useQuery('slider', fetchHeaderSliderData);

    if (isLoading) {
        <div className="Header parent">
            <div className="wrapper">
                <Slider {...settings} className='carousel'>
                    {[1, 2, 3].map((item, index) => (
                        <div key={index} className="banner">
                            <Skeleton variant='rounded' sx={{ bgcolor: 'grey.800' }} width="100%" className="back-img" />
                        </div>
                    ))}
                </Slider>
            </div>
        </div>
    }

    return (
        <div className='Header parent'>
            <div className="wrapper">
                <Slider {...settings} className='carousel'>
                    {data?.data.map((item) => (
                        <div key={item.id} className="banner">
                            <img src={item.image} alt="img" className="back-img" />
                            <div className="texts">
                                <h1 className="big-name">{getName(item)}</h1>
                                <a href={item.link} className="explore">{t("batafsil")}</a>
                            </div>
                        </div>
                    ))}
                </Slider>
            </div>
        </div>
    );
};

export default Header;