import React from 'react';
import Slider from 'react-slick';
import '../styles/HomeCategories.scss';
import { useQuery } from 'react-query';
import { Link } from 'react-router-dom';
import { Skeleton } from '@mui/material';
import { getName } from '../languages/language';
import { fetchCategoriesData } from '../api/Api';

const HomeCategories = () => {

    const settings = {
        speed: 2000,
        dots: false,
        autoplay: true,
        infinite: true,
        slidesToShow: 6,
        slidesToScroll: 3,
        cssEase: "linear",
        autoplaySpeed: 6000,
        pauseOnHover: false,
        responsive: [
            {
                breakpoint: 1000,
                settings: {
                    slidesToShow: 4,
                    slidesToScroll: 2
                }
            },
            {
                breakpoint: 767,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1
                }
            }
        ]
    };

    // data of categories

    const { isLoading, data } = useQuery('categories', fetchCategoriesData);

    if (isLoading) {
        <div className="HomeCategories parent">
            <div className="wrapper">
                <Slider {...settings} className='carousel'>
                    {[1, 2, 3, 4, 5, 6, 7, 8, 9].map((item, index) => (
                        <div key={index} className="category">
                            <Skeleton variant='rounded' sx={{ bgcolor: 'grey.800' }} width="100%" className='img' />
                            <p className="name">
                                <Skeleton variant='text' sx={{ bgcolor: 'grey.800' }} width="100%" />
                                <Skeleton variant='text' sx={{ bgcolor: 'grey.800' }} width="60%" />
                            </p>
                        </div>
                    ))}
                </Slider>
            </div>
        </div>
    }

    return (
        <div className='HomeCategories parent'>
            <div className="wrapper">
                <Slider {...settings} className='carousel'>
                    {data?.data.map((item) => (
                        <Link key={item.id} to={`/categories/${item.id}`} className="category">
                            <img src={item.image} alt="img" className="img" />
                            <p className="name">{getName(item)}</p>
                        </Link>
                    ))}
                </Slider>
            </div>
        </div>
    );
};

export default HomeCategories;