import React from 'react';
import Slider from 'react-slick';
import '../styles/HomeProducts.scss';
import { useQuery } from 'react-query';
import { Link } from 'react-router-dom';
import { Skeleton } from '@mui/material';
import { useTranslation } from 'react-i18next';
import { fetchProductsData } from '../api/Api';
import { getName } from '../languages/language';

const HomeProducts = ({ changeContactModal }) => {

    const settings = {
        speed: 2000,
        dots: false,
        autoplay: true,
        infinite: true,
        slidesToShow: 6,
        slidesToScroll: 3,
        cssEase: "linear",
        autoplaySpeed: 6000,
        pauseOnHover: false,
        responsive: [
            {
                breakpoint: 1000,
                settings: {
                    slidesToShow: 4,
                    slidesToScroll: 2
                }
            },
            {
                breakpoint: 767,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1
                }
            }
        ]
    };

    // i18next

    const { t } = useTranslation();

    // data of products

    const { isLoading, data } = useQuery('products', fetchProductsData);

    if (isLoading) {
        <div className="HomeProducts parent">
            <div className="wrapper">
                <Slider {...settings} className='carousel'>
                    {[1, 2, 3, 4, 5, 6, 7, 8, 9].map((item, index) => (
                        <div key={index} className="product">
                            <div style={{ textDecoration: "none" }}>
                                <Skeleton variant='rounded' sx={{ bgcolor: 'grey.800' }} width="100%" className="img" />
                            </div>
                            <Skeleton variant='rounded' sx={{ bgcolor: 'grey.800' }} width="100%" className="explore text" />
                        </div>
                    ))}
                </Slider>
            </div>
        </div>
    }

    return (
        <div className='HomeProducts parent'>
            <div className="wrapper">
                <h1 className="title">{t("tavfsiya")}</h1>
                <Slider {...settings} className='carousel'>
                    {data?.data.map((item) => (
                        <div key={item.id} className="product">
                            <Link to={`/products/${item.id}`} style={{ textDecoration: "none" }}>
                                <img src={item.image} alt="img" className="img" />
                                <p className="text">{getName(item)}</p>
                                <h3 className="name">{item.price} {t("sum")}</h3>
                            </Link>
                            <button className='explore text' onClick={changeContactModal}>{t("buy")}</button>
                        </div>
                    ))}
                </Slider>
            </div>
        </div>
    );
};

export default HomeProducts;