import { useEffect, useRef, useState } from 'react';
import './App.scss';
import Modal from './components/Modal';
import About from './components/About';
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import Loader from './components/Loader';
import HomeRouter from './routes/HomeRouter';
import Products from './components/Products';
import Notfound from './components/Notfound';
import Categories from './components/Categories';
import ScrollToTop from './components/ScrollToTop';
import ContactModal from './components/ContactModal';
import { QueryClient, QueryClientProvider } from 'react-query';
import { BrowserRouter, Route, Routes } from 'react-router-dom';

function App() {

  // navbar scrolling pages

  const faqPage = useRef();
  const aboutPage = useRef();
  const productsPage = useRef();
  const advantegesPage = useRef();

  function changeScrollFaq() {
    setTimeout(() => {
      window.scrollTo(0, faqPage.current.offsetTop)
    }, 100);
  };

  function changeScrollAbout() {
    setTimeout(() => {
      window.scrollTo(0, aboutPage.current.offsetTop)
    }, 100);
  };

  function changeScrollProducts() {
    setTimeout(() => {
      window.scrollTo(0, productsPage.current.offsetTop)
    }, 100);
  };

  function changeScrollAdvantages() {
    setTimeout(() => {
      window.scrollTo(0, advantegesPage.current.offsetTop)
    }, 100);
  };

  // react-query

  const queryClient = new QueryClient();

  // modal

  const [showModal, setShowModal] = useState(false);

  function changeModal() {
    setShowModal(!showModal)
  };

  // modal

  const [showContactModal, setShowContactModal] = useState(false);

  function changeContactModal() {
    setShowContactModal(!showContactModal)
  };

  // loader

  const [showLoader, setShowLoader] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      setShowLoader(false)
    }, 2000);
  }, []);

  return (
    <QueryClientProvider client={queryClient}>
      <BrowserRouter>
        <ScrollToTop />
        {showLoader &&
          <Loader />
        }
        <Navbar changeScrollFaq={changeScrollFaq} changeScrollAbout={changeScrollAbout} changeScrollProducts={changeScrollProducts} changeScrollAdvantages={changeScrollAdvantages} />
        <Routes>
          <Route exact path='/' element={<HomeRouter changeModal={changeModal} changeContactModal={changeContactModal} faqPage={faqPage} aboutPage={aboutPage} productsPage={productsPage} advantegesPage={advantegesPage} />} />
          <Route path='/about' element={<About />} />
          <Route path='/categories/:id' element={<Categories />} />
          <Route path='/products/:id' element={<Products changeContactModal={changeContactModal} />} />
          <Route path='*' element={<Notfound />} />
        </Routes>
        <Footer changeScrollFaq={changeScrollFaq} changeScrollAbout={changeScrollAbout} changeScrollProducts={changeScrollProducts} changeScrollAdvantages={changeScrollAdvantages} />
        {showContactModal &&
          <ContactModal changeContactModal={changeContactModal} changeModal={changeModal} />
        }
        {showModal &&
          <Modal changeModal={changeModal} />
        }
      </BrowserRouter>
    </QueryClientProvider>
  );
}

export default App;
