import React, { useState } from 'react';
import '../styles/FAQ.scss';
import { useQuery } from 'react-query';
import { Skeleton } from '@mui/material';
import { fetchFaqsData } from '../api/Api';
import { useTranslation } from 'react-i18next';
import { AddRounded } from '@mui/icons-material';
import { getAnswer, getQuestion } from '../languages/language';

const FAQ = () => {

    const [activeIndex, setActiveIndex] = useState(null);

    // i18next

    const { t } = useTranslation();

    // data of FAQ

    const { isLoading, data } = useQuery('faq', fetchFaqsData);

    const dataFAQ = data?.data;

    if (isLoading) {
        <div className="FAQ parent">
            <div className="wrapper">
                <h1 className="title">
                    <Skeleton variant='text' sx={{ bgcolor: 'grey.800' }} width="50%" className="sub-title" />
                </h1>
                {[1, 2, 3, 4, 5]?.map((item, index) => (
                    <div key={index} className="bar">
                        <div className="question">
                            <Skeleton variant='text' sx={{ bgcolor: 'grey.800' }} width="100%" className="sub-title" />
                        </div>
                    </div>
                ))}
            </div>
        </div>
    }

    return (
        <div className="FAQ parent">
            <div className="wrapper">
                <h1 className="title">{t("faq")}</h1>
                {dataFAQ?.map((item) => (
                    <div key={item.id} className="bar">
                        <div className="question">
                            <h3 className="sub-title">{getQuestion(item)}</h3>
                            <AddRounded onClick={() => activeIndex != dataFAQ.indexOf(item) ? setActiveIndex(dataFAQ.indexOf(item)) : setActiveIndex(null)} className={`icon ${activeIndex == dataFAQ.indexOf(item) && "active-icon"}`} />
                        </div>
                        <div className={`answer ${activeIndex == dataFAQ.indexOf(item) && "active-answer"}`}>
                            <p className="text">{getAnswer(item)}</p>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default FAQ;