import { Close } from '@mui/icons-material';
import React, { useState } from 'react';
import '../styles/ContactModal.scss';

const ContactModal = ({ changeModal, changeContactModal }) => {

    // input values

    const [nameValue, setNameValue] = useState("");
    const [numberValue, setNumberValue] = useState("");

    // invalid input states

    const [invalidName, setInvalidName] = useState(false);
    const [invalidNumber, setInvalidNumber] = useState(false);

    // change values and invalids functions

    function changeNumber(item) {
        setNumberValue(item);
        setInvalidNumber(false);
    };

    function changeName(item) {
        setNameValue(item);
        setInvalidName(false);
    };

    // send message to telegram bot

    let bot = {
        TOKEN: "6298069139:AAF9387TSMXx1IbHtHDXFP_9cvIqsoiR1RY",
        chatID: "-1001869181628",
        message: `%0AIsmi: ${nameValue}; %0ATelefon raqami: ${numberValue}.`
    };

    function sendMessage() {
        if (nameValue === "") {
            setInvalidName(true)
        } else if (numberValue === "") {
            setInvalidNumber(true)
        } else {
            fetch(`https://api.telegram.org/bot${bot.TOKEN}/sendMessage?chat_id=${bot.chatID}&text=${bot.message} `, {
                method: "GET"
            })
                .then(success => {
                    console.log('send message')
                }, error => {
                    console.log(error)
                })

            changeModal();
            setNameValue("");
            setNumberValue("");
            changeContactModal();
        }
    };

    return (
        <div className='ContactModal'>
            <div className="contr" onClick={changeContactModal}></div>
            <div className="controls">
                <Close className="icon" onClick={changeContactModal} />
                <input type="text" className={`forms-control text ${invalidName && "red-line"}`} value={nameValue} onChange={(e) => changeName(e.target.value)} placeholder="Ism" />
                <input type="number" className={`forms-control text ${invalidNumber && "red-line"}`} value={numberValue} onChange={(e) => changeNumber(e.target.value)} placeholder="Telefon raqam" />
                <button className='send-btn name' onClick={sendMessage}>Yuborish</button>
            </div>
        </div>
    );
};

export default ContactModal;